main, footer {
  background: #f7f7f7;
}
main {
  padding-bottom: 50px;
}

.news-header {
  width: calc(100% + 40px);
  margin: -24px 0 50px -20px;
  padding: 74px 20px 32px 20px;
  background: #fde033;
  border-radius: 0 0 40px 40px;
}

.news-header .art {
  height: 400px;
}

.news-header .index {
  display: inline-block;
  padding: 2px 8px 4px 8px;
  margin: 34px 0 8px 0;
  color: #ffffff;
  background: #242424;
  font-size: 14px;
}

.news-header .flex {
  flex-direction: column;
}

.news-header .title {
  padding-right: 40px;
  margin-bottom: 8px;
  font-family: "Druk Wide";
  font-weight: 700;
  font-size: 22px;
  line-height: 28px;
}

p {
  max-width: 440px;
  margin: 16px auto;
}
p.news-title {
  margin-top: 100px;
  font-family: "Druk Wide";
  font-size: 24px;
  line-height: 30px;
}
p.news-title.larger {
  max-width: 680px;
}
p img {
  margin: 80px 0 20px 0;
  object-fit: contain;
}
p a {
  color: #242424;
}
.game {
  width: fit-content;
  margin: 24px auto;
}
.factoid {
  width: fit-content;
  max-width: 800px;
  margin: 40px auto 0 auto;
  font-size: 24px;
  line-height: 38px;
}
.factoid > div {
  margin-bottom: 30px;
}
.factoid > div span {
  display: inline-block;
  margin-bottom: 20px;
  font-family: "Metanart";
  font-size: 70px;
  line-height: 1em;
  color: #fde033;
}
.factoid > div div {
  max-width: 260px;
}

.sliders {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1000px;
  margin: 60px auto 0 auto;
}
.sliders > div {
  width: 100%;
  margin-bottom: 70px;
  text-align: center;
}
.sliders .slider-title {
  font-family: "Metanart";
  font-size: 32px;
}
.sliders input {
  width: 100%;
  height: 2px;
  margin: 24px 0 16px 0;
  -webkit-appearance: none;
  background: #242424;
  outline: none;
}
.sliders input::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  background: url(../assets/images/slider.png) no-repeat center center/cover;
  cursor: pointer;
}

.sliders input::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background: url(../assets/images/slider.png) no-repeat center center/cover;
  cursor: pointer;
}
.sliders .slider-text {
  font-size: 20px;
}

.plans {
  width: fit-content;
  margin: 0 auto;
}
.plans div {
  position: relative;
  width: 100%;
  max-width: 350px;
  height: fit-content;
  padding: 24px 20px;
  background: #fde033;
  font-family: "Druk Wide";
  font-size: 20px;
  line-height: 24px;
}
.plans div:nth-child(1) {
  margin: 60px 0 80px 0;
}
.plans div:nth-child(3) {
  margin: 54px 0 58px 0;
}
.plans div:nth-child(4) {
  margin: 0 0 100px 0;
}
.plans div:nth-child(1):before {
  content: "";
  position: absolute;
  top: -45px;
  right: 9px;
  width: 36px;
  height: 36px;
  background: url(../assets/images/rects-1.png) no-repeat center center/cover;
}
.plans div:nth-child(1):after {
  content: "";
  position: absolute;
  bottom: -66px;
  left: 0;
  width: 176px;
  height: 67px;
  background: url(../assets/images/rects-2.png) no-repeat center center/cover;
}
.plans div:nth-child(3):after {
  content: "";
  position: absolute;
  bottom: -35px;
  left: 50px;
  width: 36px;
  height: 36px;
  background: url(../assets/images/rects-1.png) no-repeat center center/cover;
  transform: rotate(90deg);
}
.plans div:nth-child(4):after {
  content: "";
  position: absolute;
  top: -48px;
  right: -18px;
  width: 148px;
  height: 66px;
  background: url(../assets/images/rects-3.png) no-repeat center center/cover;
}

.avito-id-img {
  width: 100%;
  max-width: 335px;
  margin: 20px auto 30px auto;
}
.avito-id-img img {
  width: 100%;
  object-fit: contain;
}

.vas-block {
  position: relative;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  max-width: 340px;
  margin: 50px auto;
}
.vas-block > img {
  width: 100%;
}
.vas-block > div {
  max-width: 320px;
  margin: 30px 0;
}
.vas-block > div > img {
  width: 87px;
  margin-right: 16px;
  object-fit: contain;
}

.vas-block-2 {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: fit-content;
  margin: 0 auto;
  max-width: 1150px;
}
.vas-block-2 > div {
  position: relative;
  width: 100%;
  max-width: 360px;
  height: fit-content;
  padding: 24px 20px 40px 20px;
  background: #ffffff;
}
.vas-block-2 .index {
  color: #fde033;
  font-family: 'Metanart';
  font-size: 90px;
  line-height: 1.4em;
}
.vas-block-2 .muted {
  opacity: 0.5;
  font-size: 14px;
  line-height: 1.4em;
}
.vas-block-2 div:nth-child(1) {
  margin-top: 96px;
}
.vas-block-2 div:nth-child(2) {
  margin: 30px 0;
}
.vas-block-2 div:nth-child(3) {
  margin-bottom: 30px;
}
.vas-block-2 div:nth-child(1):before {
  content: "";
  position: absolute;
  top: -50px;
  left: -18px;
  width: 138px;
  height: 50px;
  background: url(../assets/images/rects-4.png) no-repeat center center/cover;
}
.vas-block-2 div:nth-child(3):after {
  content: "";
  position: absolute;
  bottom: -112px;
  right: -18px;
  width: 135px;
  height: 112px;
  background: url(../assets/images/rects-5.png) no-repeat center center/cover;
}

.news-next-link {
  display: block;
  position: relative;
  width: calc(100% + 40px);
  margin-left: -20px;
  margin-top: 174px;
  padding: 32px 24px;
  background: #fde033;
  border-radius: 20px;
  color: #242424;
  text-decoration: none;
}
.news-next-link .label {
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.news-next-link div {
  max-width: 580px;
}
.news-next-link .title {
  margin: 8px 0;
  font-family: "Druk Wide";
  font-size: 20px;
}
.news-next-link .arrow {
  justify-content: center;
  width: 70px;
  height: 70px;
  margin-top: 60px;
  border-radius: 50%;
  background: #242424;
}
.news-next-link .arrow img {
  width: 60%;
}

.scroll-texts {
  position: relative;
  width: 100%;
}
.scroll-texts > div {
  position: relative;
  z-index: 9;
  width: 85vw;
  max-width: 335px;
  margin: 0 auto 75vh auto;
  padding: 16px 16px 32px 16px;
  background: rgba(255, 255, 255, 0.9);
}
.scroll-texts > div.centered {
  display: flex;
  flex-direction: column;
  align-items: center
}
.scroll-texts > div.small-margin {
  margin-bottom: 20px;
}
.scroll-texts > div.empty-block {
  visibility: hidden;
}
.scroll-texts > div.empty-block-margin {
  margin-bottom: 40vh;
}
.scroll-texts > div ul {
  padding-left: 24px;
}
.scroll-texts > div img {
  display: block;
  width: 54px;
  object-fit: contain;
  margin-bottom: 10px;
}
.scroll-texts > div span {
  display: inline-block;
  padding: 0 2px 0 6px;
  font-family: "Metanart";
}
.scroll-texts > div span.yellow {
  background: #fde033;
}
.scroll-texts > div span.blue {
  background: #5ea9c1;
  color: #fde033;
}
.scroll-texts > div span.red {
  background: #fc7330;
  color: #ffffff;
}

.scroll-texts .black-label {
  width: fit-content;
  margin-top: 50px;
  padding: 6px 20px;
  border-radius: 100px;
  background: #242424;
  color: #ffffff;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.phones-texts > div {
  margin-bottom: 50vh;
}

