.phones-texts .cover {
  max-width: unset;
  margin: 0 auto;
  background: none;
  font-family: "Metanart";
  font-size: 90px;
  line-height: 110px;
  letter-spacing: -0.1em;
  text-align: center;
}
.phones-texts .cover span {
  display: inline;
}
.phones-texts .cover span:first-child {
  color: #fde033;
}
.phones-texts .cover span:last-child {
  color: #dddddd;
}
.phones-texts .black-label.fixed,
.phones-texts .cover.fixed {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  pointer-events: none;
}
.phones-texts .cover.fixed {
  top: 100px;
}

.questions {
  width: 100%;
  max-width: 670px;
  margin: 40px auto 0 auto;
}
.questions div {
  padding: 16px 16px 32px;
  margin: 8px 0px;
}
.questions div:nth-child(2n + 1) {
  font-weight: bold;
  background: #fde033;
  border-radius: 20px 0px 20px 20px;
}
.questions div:nth-child(2n) {
  background: #ffffff;
  border-radius: 0px 20px 20px 20px;
}

@media screen and (min-width: 1024px) {
  .news-header {
    width: calc(100% + 80px);
    margin-left: -40px;
    padding: 74px 40px;
  }

  .news-header .flex {
    flex-direction: row;
    align-items: flex-start;
  }

  .news-header .title {
    font-size: 64px;
    line-height: 64px;
  }

  .news-header .subtitle {
    max-width: 325px;
    margin-top: 16px;
  }

  .plans {
    display: flex;
    margin: 60px auto 80px auto;
  }
  .plans > div:nth-child(n) {
    margin: 0;
    margin-right: 20px;
    font-size: 16px;
  }
  .plans > div:last-child {
    margin-right: 0;
  }

  .factoid {
    display: flex;
    justify-content: space-between;
  }
  .factoid > div {
    margin: 0 20px;
  }
  .factoid > div span {
    font-size: 90px;
    margin-top: 40px;
  }

  .sliders > div {
    width: 50%;
    padding: 0 60px;
  }

  .avito-id-img {
    max-width: 680px;
    margin-top: 40px;
  }

  .vas-block {
    flex-direction: row;
    max-width: 920px;
  }
  .vas-block > div {
    margin: 30px;
  }

  .vas-block-2 {
    flex-direction: row;
  }
  .vas-block-2 div:nth-child(n) {
    margin-top: 40px;
  }
  .vas-block-2 div:nth-child(2) {
    margin-left: 24px;
    margin-right: 24px;
  }

  .news-next-link {
    width: 100%;
    margin-left: 0;
    margin-top: 200px;
    padding: 32px 16px;
  }
  .news-next-link .title {
    font-size: 32px;
    line-height: 34px;
  }
  .news-next-link .arrow {
    position: absolute;
    top: 50%;
    right: 16px;
    width: 130px;
    height: 130px;
    margin-top: 0;
    transform: translateY(-50%);
  }

  .fire-texts > div.small-margin {
    margin-bottom: 36px;
  }

  .phones-texts .cover {
    font-size: 149px;
    line-height: 204px;
  }
}

@media screen and (min-width: 1280px) {
  .plans > div:nth-child(n) {
    font-size: 24px;
  }
}


/* ----- games ----- */
.game canvas {
  margin: 0 20px;
  width: 376px;
  height: 768px;
  border: 1px solid #000;
}

@media screen and (max-width: 768px) {
  .game {
    display: flex;
    margin: 20px 0;
    width: 100%;
    height: auto
  }

  .game canvas {
    margin: 0 auto;
    width: auto !important;
    max-width: 100%;
    max-height: calc(var(--vh, 1vh) * 100) !important;
    height: auto !important;
  }
}
