  background: #242424;
  outline: none;
  margin: 24px 0 16px;
}

.sliders input::-webkit-slider-thumb {
  appearance: none;
  width: 20px;
  height: 20px;
  cursor: pointer;
  background: url("slider.9b0fcf5c.png") center / cover no-repeat;
}

.sliders input::-moz-range-thumb {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background: url("slider.9b0fcf5c.png") center / cover no-repeat;
}

.sliders .slider-text {
