}

.plans div:nth-child(1):before {
  content: "";
  width: 36px;
  height: 36px;
  background: url("rects-1.6ab4c9da.png") center / cover no-repeat;
  position: absolute;
  top: -45px;
  right: 9px;
}

.plans div:nth-child(1):after {
  content: "";
  width: 176px;
  height: 67px;
